.a-simple-menu {
    &__list {
        display: flex;
        padding: 0;
        margin: 0;
    }

    &__item {
        &:first-child {
            & .a-simple-menu__link {
                padding-left: 0;
            }
        }

        &:last-child {
            & .a-simple-menu__link {
                padding-right: 0;
            }
        }

        &:hover .a-simple-menu__sub-menu {
            opacity: 1;
            visibility: visible;
        }
    }

    &__link {
        display: inline-flex;
        letter-spacing: 0;
        padding: 19px 10px 18px;
        text-transform: uppercase;
        text-decoration: none;
        color: var(--main-text-color);
        line-height: 1.1;
        //position: relative;

        &:hover,
        &_active {
            color: var(--accent-color);
            text-decoration: none;
        }
    }

    &__sub-menu {
        position: absolute;
        opacity: 0;
        visibility: hidden;
        transition: opacity .3s;
        margin-top: -1px;

        &_active {

        }
    }
}

.a-sub-menu {
    padding: 33px 39px 39px 39px;
    background: #fff;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.08);

    &__list {
        padding: 0;
    }

    &__link {
        color: var(--muted-text-color);

        &:hover {
            color: var(--accent-color);
        }
    }
}
